import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import '../index.css';

function SizeableImage(props) {
  return (
    <div className={props.bgcolor}>
    <div className={props.smallimage}>
    <Container>
      <div className="w-100 sizeimagediv">
        <img src={props.imagesource} className="sizeableimage" width={props.width} alt={props.imagesource}/>
      </div>
    </Container>
    </div>
    </div>
  );
}

export default SizeableImage;
