import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Spacer from '../components/Spacer';
import '../index.css';

function CenteredBlurb (props) {
  return (
    <div className={props.bgcolor}>
    <Container>
      <div>
        <Row className="justify-content-md-center text-center">
          <Col xs lg="8">

            <Spacer/>
            <Spacer/>
            {props.title && <h2 class="headertext">{props.title}</h2>}
            {props.smalltitle && <h3 class="subheadertext">{props.smalltitle}</h3>}

            <div className="SmallSpacer"/>
            {props.text && <p className="bodytext font-weight-light">{props.text}</p>}
            <Spacer/>
            <Spacer/>

          </Col>
        </Row>
      </div>
    </Container>
    </div>
  );
}

export default CenteredBlurb
