import React, { Component } from 'react';

import '../index.css';

class MenuItem extends Component {
  constructor(props) {
    super();
    this.anchorTarget = null;
    this.handleClick = this.handleClick.bind(this);
  }
  handleClick(e) {
    e.preventDefault();
    this.anchorTarget.scrollIntoView({behavior:"smooth", block:"start"});
  }
  componentDidMount() {
    this.anchorTarget = document.getElementById(this.props.itemName);
  }

  render() {

   if (this.props.active) {
     return (
         <a className="activecontents" href={'#' + this.props.itemName} onClick={this.handleClick}
         aria-label={'Scroll to ' + this.props.itemName}>
           {this.props.itemName}
           {this.props.anchorTarget}
         </a>
       )
   }


    else {
      return (
        <a className="inactivecontents" href={'#' + this.props.itemName} onClick={this.handleClick}
        aria-label={'Scroll to ' + this.props.itemName}>
          {this.props.itemName}
          {this.props.anchorTarget}
        </a>
      )
    }
  }
}
export default MenuItem;
