import React, { Component } from 'react';
import MenuItem from './MenuItem';


import '../index.css';

class Menu2 extends Component {


  constructor(props) {
    super();
    this.sections = {
      Summary: 0,
      Research: 0,
      Architecture: 0,
    }
    this.state = {
      activeMenuItem: {
        top: '',
        Summary: '',
        Research: '',
        Architecture: '',
      }
    }
    this.handleScroll = this.handleScroll.bind(this);
    this.getAnchorPoints = this.getAnchorPoints.bind(this);
  }
  handleScroll() {
    const curPos = window.scrollY;
    let curSection = null;
    if(curPos > (this.sections.Architecture-30)) {
      curSection = 'Architecture';
    }
    else if(curPos > (this.sections.Research-30)) {
      curSection = 'Research';
    }  else if(curPos > (this.sections.Summary-30)) {
      curSection = 'Summary';
    } else {
      curSection = 'top';
    }

    if(this.state.activeMenuItem[curSection] !== "active") {
      const newMenuState = {
        top: '',
        Summary: '',
        Research: '',
        Architecture:'',
      }
      newMenuState[curSection] = "active";
      this.setState({ activeMenuItem: newMenuState });

    }
  }
  getAnchorPoints() {
    const curScroll = window.scrollY;
  //  if (document.getElementById(key)) {
      for(var key in this.sections) {
        this.sections[key] = document.getElementById(key).getBoundingClientRect().top + curScroll;

//    }
    }

    /* Get the pixel height of the viewport */

    const viewPortHeight = Math.max(document.documentElement.clientHeight, window.innerHeight || 0);

    /* Get the pixel-depth of the bottom of the page */

    const bottom = document.body.offsetHeight;

    /* If max-scroll is less than the pixel-depth of Section 3, then adjust the pixel-depth of Section 3 to be 50px higher than max scroll depth ... allowing it to be an active destination */

//    if(viewPortHeight > (bottom - this.sections.Personas)) {
//      this.sections.Section3 = bottom - viewPortHeight - 50;
//    }
    this.handleScroll();
  }

  componentDidMount() {

    window.addEventListener('scroll', this.handleScroll);

    /* When window resizes, get new scroll-depth positions of anchors */
    window.addEventListener('resize', this.getAnchorPoints);

    this.getAnchorPoints();
  }
  componentDidUpdate() {
    this.getAnchorPoints();
  }

  componentWillUnmount(){
    window.removeEventListener('scroll', this.handleScroll);
    window.removeEventListener('resize', this.getAnchorPoints);
  }
  render() {
    const menuItems = [];
    for(var key in this.sections) {
      menuItems.push(<MenuItem itemName={key} active={this.state.activeMenuItem[key]} />);
    }
    return (

        <ul>
          {menuItems}
        </ul>

    );
  }
}
export default Menu2;
