import React from 'react';
import Psummary from '../components/Psummary';
import CenteredBlurb from '../components/CenteredBlurb';
import Spacer from '../components/Spacer';
import FullImage from '../components/FullImage';
import ScrollTop from '../components/ScrollTop';
import Menu3 from '../components/Menu3';
import HeroImage from '../components/HeroImage';
import CenteredTitle from '../components/CenteredTitle';
import Container from 'react-bootstrap/Container';
import BackButton2 from '../components/BackButton2';
import HeroLeftRight from '../components/HeroLeftRight';
import SizeableImage from '../components/SizeableImage';
import Divider from '../components/Divider';
import BackButton from '../components/BackButton';

import '../index.css';

import calendar_animation from '../assets/images/planchat/calendar_animation.gif';
import current_photoalbum from '../assets/images/planchat/current_photoalbum.png';
import edit_itinerary from '../assets/images/planchat/edit_itinerary.png';
import event_album from '../assets/images/planchat/event_album.png';
import event_animation from '../assets/images/planchat/event_animation.gif';
import itinerary_chat from '../assets/images/planchat/itinerary_chat.png';
import itinerary_timeline from '../assets/images/planchat/itinerary_timeline.png';
import new_event from '../assets/images/planchat/new_event.png';
import new_photoalbum from '../assets/images/planchat/new_photoalbum.png';
import past_events from '../assets/images/planchat/past_events.png';
import pcaccount_page from '../assets/images/planchat/pcaccount_page.png';
import pclogo_1 from '../assets/images/planchat/pclogo_1.png';
import pclogo_2 from '../assets/images/planchat/pclogo_2.png';
import pclogo_3 from '../assets/images/planchat/pclogo_3.png';
import pclogo_4 from '../assets/images/planchat/pclogo_4.png';
import planapp_architecture from '../assets/images/planchat/planapp_architecture.png';
import planchat_hero from '../assets/images/planchat/planchat_hero.png';
import planchat_wireframe from '../assets/images/planchat/planchat_wireframe.png';
import planchat_event_new from '../assets/images/planchat/event_animation_new.gif';
import pc_core from '../assets/images/planchat/pc_core.png';

function PlanchatPage(props) {

  return (
    <div>
    <div className="divmovedown">
        <BackButton2/>

          <div id="Summary" className="anchor"/>
          <Container>
          <HeroImage imagesource={planchat_hero}/>


          <Container className="generaltextcontainer">
            <p className="herotext">PlanChat Mobile App</p>
            <p className="subtitletext">Integrating a chatroom with an event itinerary</p>
            <p>PlanChat is an app that helps with planning events online. By combining an automatically-updating
            itinerary with a chatroom, everybody can easily be kept
            on the same page. The scope of this project included conducting user research to identify
            areas where current tools could be improved upon, designing UX solutions to address these problems,
            articulating the app architecture, and creating proof-of-concept assets.</p>
          </Container>
          <Spacer />
          <SizeableImage imagesource={itinerary_chat} width="60%"/>
          <Container className="generaltextcontainer">
            <p className="captiontitle">Itinerary Item (left) and Chatroom Poll (right)</p>
            <p className="captiontext">
            </p>
          </Container>

          <Divider />

          <Container className="generaltextcontainer">
            <p className="paragraphtitle">User research to identify areas of improvement within the current tools</p>
            <p>This project began with conducting user interviews to identify
            common pain points when planning events. The focus of these interviews
            was to find out the platforms that people commonly use, as well as any perceived
            limitations of these existing tools.<br/><br/>
            Some of the most common platforms that were used included Discord and
            Facebook Events. For Discord users, a dedicated server is often created for a
            specific event, with some people also using pinned message to track updates within these rooms.
            However, keeping track of changes to a plan remained a common source of confusion
            for many people. It was determined that this was due to the fact that
            pinned messages are not designed to contain detailed summaries in an
            easy-to-update format. Furthermore, Facebook Events could sometimes feel
            disjointed due to the fact that the event's Messenger chatroom was disconnected from
            the event page itself.</p>
            <br/>
            <p className="paragraphtitle">The issue: how do we keep everybody on the same page?</p>
            <p>The research indicated that the most significant challenge of planning events online
            was keeping everybody up-to-date. It is not only difficult to discuss plans in general
            due to the fact that attendees may not always be present in chatrooms, but it is also hard
            to keep track of an itinerary once a decision has been made. It was decided that this issue
            would be the main area that is addressed by this project.
            </p>
          </Container>

          <Divider />
          <Container className="generaltextcontainer">
            <p className="paragraphtitle">Designing the UX solution</p>
            <p>Development of the solution began by articulating the core tools that are required
            to plan an event. It was found that two interconnected elements would be needed: <br/><br/>
            <b>1. A chatroom</b><br/>
            A platform to discuss ideas, suggest activites, and discuss changes would be essential.
            A chatroom would be the most appropriate platform of communication due to its flexibility
            and similarity to existing tools.<br/><br/>
            <b>2. An itinerary</b><br/>
            Providing an easily accessible itinerary to users allows them to easily view the latest
            iteration of an event at a quick glance, without having to sort through pages of new messages,
            sifting through pinned messages, or asking another attendee for a summary.
            <br/><br/>
            It was essential that these two elements were integrated with one another for the solution
            to be effective - otherwise, it would simply result in more manual work for the event creator,
            who would need to manually update the itinerary every time a decision is made.
            </p>
          </Container>
          <SizeableImage imagesource={pc_core} width="60%"/>
          <Spacer/>
          <Divider />

          <Container className="generaltextcontainer">
            <p className="paragraphtitle">Developing the page architecture</p>
            <p>
            It was determined that events would be managed using Event Pages. Every event would have its own individual page, where
            users can view updates and invite attendees. Furthermore, every Event Page would have its own associated Chatroom and Itinerary - changes to a plan
            can be suggested in the Chatroom using Polls, and Poll results automatically update Itinerary
            Items to be easily viewed at-a-glance.
            <br/><br/>

            Since the PlanChat app is centered around creating and managing Event Pages, the remaining architecture
            would need to make it easy to access and edit these pages.
            Existing events are naturally to easy access as they are listed on the Home Page.
            However, it was also important for users to be able to create events from various other
            pages of the app. Therefore, the design allows users to create a brand new event from (1) the Home Page and
            (2) the Chat Page. Users can also duplicate an old event from a Past Event Page.
            </p>
          </Container>
          <SizeableImage imagesource={planapp_architecture} width="90%"/>
          <Spacer/><Spacer/><Spacer/><Spacer/>
          <SizeableImage imagesource={planchat_wireframe} width="100%"/>
          <Spacer/><Spacer/><Spacer/>
          <Divider/>
          <Container className="generaltextcontainer">
            <p className="paragraphtitle">A secondary problem: the challenge of centralizing everybody's photos</p>
            <p>Another issue that was identified with available tools was centralizing photos
            after an event. Currently, common solutions included a designated photographer
            creating a Google Drive of all the trip's photos, or attendees individually
            uploading photos to different locations and sharing the download links. However, this requires users to
            navigate to several seperate locations to collect all of an event's photos.
            </p>
          </Container>

          <SizeableImage imagesource={current_photoalbum} width="70%"/>
          <Spacer/><Spacer/><Spacer/>
          <Container className="generaltextcontainer">
            <p className="paragraphtitle">The proposed solution: Past Event Pages</p>
            <p>The proposed solution was to provide users an option to upload photos
            to an event page after it is completed. With this, it becomes easy to centralize
            everybody's photos in a single easy-to-access location.
            </p>
          </Container>
          <SizeableImage imagesource={new_photoalbum} width="50%"/>

          <Spacer /><Spacer /><Spacer />
          <Divider/>

          <CenteredBlurb title="Final Mockups"
          />

          <SizeableImage imagesource={calendar_animation} width="40%"/>

          <Container className="generaltextcontainer">
            <p className="captiontitle">The Home Page: view all of your upcoming events in a list or
            calendar layout</p>
            <p className="captiontext">The Home Page shows all of your upcoming events
            and can be viewed in two different layouts,
            making sure you never miss plans that are coming up.
            </p>
          </Container>


          </Container>

          <div id="Events" className="anchor"/>


          <Spacer />
          <Divider />

          <Spacer />
          <Container>

          <SizeableImage imagesource={planchat_event_new} width="33%"/>
          <Container className="generaltextcontainer">
            <p className="captiontitle">The Event Page</p>
            <p className="captiontext">The Event Page is the hub that connects the Itinerary with
            the event's Chatroom. Upon opening an event, users see its most recent updates
             - chat notifications, pinned messages, and the most recent message.
            As users scroll down, they see the full Itinerary Timeline. Each Itinerary
            Item can be opened to view details on its location and attendees.
            </p>


          </Container>



          </Container>
          <Spacer />
          <Divider />
          <Spacer/>
          <Spacer/>

          <SizeableImage imagesource={itinerary_timeline} width="65%"/>

          <Container className="generaltextcontainer">
            <p className="captiontitle">The Itinerary Timeline</p>
            <p className="captiontext">The Itinerary Timeline shows all of the event's activities
            as individual items. Often, people may not be able to attend the entire event.
            By dividing an event into seperate activities, it is easy for users
            to see exactly what is happening, when it is
            happening, and who is going.
            </p>
          </Container>


          <Divider />

          <Spacer/><Spacer/><Spacer/>
          <SizeableImage imagesource={edit_itinerary} width="95%"/>

          <Container className="generaltextcontainer">
            <p className="captiontitle">Suggesting a change to an activity</p>
            <p className="captiontext">
            </p>
          </Container>


          <Divider />
          <Spacer/><Spacer/>
          <SizeableImage imagesource={new_event} width="75%"/>

          <Container className="generaltextcontainer">
            <p className="captiontitle">Creating a new event</p>
            <p className="captiontext">Events are easily created by tapping the plus icon on the bottom
            menu bar.
            </p>
          </Container>
          <Divider />
          <Spacer /><Spacer /><Spacer />

          <SizeableImage imagesource={event_album} width="80%"/>
          <Container className="generaltextcontainer">
            <p className="captiontitle">Photos are centralized in the Past Event Page
            </p>
          </Container>
          <Divider/>
          <Spacer/><Spacer/>

          <FullImage imagesource={pcaccount_page}/>

          <Container className="generaltextcontainer">
            <p className="captiontitle">Notifications are centralized in the Account Centre</p>
            <p className="captiontext">Event invitations, chat notifications, and friend invitations
            are accessed in the Account Centre.
            </p>
          </Container>


          <div id="Research" className="anchor"/>
          <div id="Architecture" className="anchor"/>

          <div id="Branding" className="anchor"/>

          <ScrollTop />


    </div>
    </div>
  );
}

export default PlanchatPage
