import React from 'react';
import Psummary from '../components/Psummary';
import CenteredBlurb from '../components/CenteredBlurb';
import Spacer from '../components/Spacer';
import FullImage from '../components/FullImage';
import ScrollTop from '../components/ScrollTop';
import Menu2 from '../components/Menu2';
import HeroImage from '../components/HeroImage';
import CenteredTitle from '../components/CenteredTitle';
import Container from 'react-bootstrap/Container';
import BackButton2 from '../components/BackButton2';
import Divider from '../components/Divider';
import SizeableImage from '../components/SizeableImage';
import '../index.css';

import decibel_logo from '../assets/images/decibel/decibel_logo.png';
import decibel_diagram from '../assets/images/decibel/decibel_diagram.png';
import decibel_1 from '../assets/images/decibel/decibel_1.png';
import decibel_2 from '../assets/images/decibel/decibel_2.png';
import decibel_3 from '../assets/images/decibel/decibel_3.png';
import decibel_4 from '../assets/images/decibel/decibel_4.png';

import decibel_proto_mobile from '../assets/images/decibel/decibel_proto_mobile.png';

function DecibelPage(props) {

  return (
    <div>
    <Container>
    <Spacer/><Spacer/><Spacer/>
    <SizeableImage imagesource={decibel_logo} width="50%"/>
    <Spacer/><Spacer/><Spacer/><Spacer/><Spacer/>
    <div className="DecibelBlurbDiv">
      <h3 className="DecibelSubtitle">An app to help roommates manage sound levels</h3><br/>
      <p className="DecibelBlurbText">We all know the pain of getting on each others' nerves. Decibel is an app that tells you when you're being too loud - before your
      roommate stomps across the house to do the same.</p>
    </div>
    <Spacer/><Spacer/><Spacer/><Spacer/>
    <Divider/>
    <Spacer/><Spacer/><Spacer/>
    <SizeableImage imagesource={decibel_diagram} width="70%"/>
    <Spacer/><Spacer/><Spacer/>
    <Divider/><Spacer/><Spacer/>
    <div className="protomobile">
      <SizeableImage imagesource={decibel_proto_mobile} width="60%"/>
    </div>
    <div className="decibelcenteredtext">
    <p className="greytext"><b>Prototype</b></p>
    </div>
    <div className= "protocontainer">
      <iframe className= "proto"
      src="https://framer.com/embed/Decibel-Prototype--loOfiQIkHzt9z0bdxyfh/EtmYk_avt?highlights=0" allowfullscreen></iframe>
    </div>
    <Spacer/><Spacer/><Spacer/>

    <SizeableImage imagesource={decibel_1} width=""/>
    <Container className="generaltextcontainer">
      <p>During Quiet Hours, Decibel sends you a notification if you exceed the sound threshold requested by your roommate.
      </p>
    </Container>
    <Spacer/><Spacer/><Spacer/>


    <SizeableImage imagesource={decibel_3} width=""/>
    <Container className="generaltextcontainer">
      <p>Set different sound thresholds depending on the situation, or define your own custom decibel level.
      </p>
    </Container>
    <Spacer/><Spacer/><Spacer/>
    <SizeableImage imagesource={decibel_2} width=""/>
    <Container className="generaltextcontainer">
      <p>Easily turn off upcoming Quiet Hours when you won't be home or if you don't need the reminders.
      </p>
    </Container>
    <Spacer/><Spacer/><Spacer/>
    <SizeableImage imagesource={decibel_4} width=""/>
    <Container className="generaltextcontainer">
      <p>Decibel keeps a history of your sound levels to help you keep track of activities that are causing the most noise.
      </p>
    </Container>
    <Spacer/><Spacer/><Spacer/>
    </Container>
    </div>
  );
}

export default DecibelPage
