import React from 'react';
import Container from 'react-bootstrap/Container';

import '../index.css';


function FullImage(props) {
  return (
    <div className={props.bgcolor}>
    <div className={props.smallimage}>
    <Container>
      <div className="w-100">
        <img src={props.imagesource} className="fillimage" width={props.width} alt={props.imagesource}/>
      </div>
    </Container>
    </div>
    </div>
  );
}

export default FullImage
