import React from 'react';
import Psummary from '../components/Psummary';
import HeroLeftRight from '../components/HeroLeftRight';
import CenteredBlurb from '../components/CenteredBlurb';
import Spacer from '../components/Spacer';
import HeroImage from '../components/HeroImage';
import MenuIos from '../components/MenuIos';
import ScrollTop from '../components/ScrollTop';
import CenteredTitle from '../components/CenteredTitle';
import BackButton from '../components/BackButton';
import Container from 'react-bootstrap/Container';

import readingbench from '../assets/images/readingbench/reading_bench.png';
import readingdetail from '../assets/images/readingbench/readingdetail.png';
import readingparts from '../assets/images/readingbench/readingparts.png';
import readingwide from '../assets/images/readingbench/readingwide.png';

import bandmain from '../assets/images/bandchair/bandmain.png';
import banddetail1 from '../assets/images/bandchair/banddetail1.png';
import banddetail2 from '../assets/images/bandchair/banddetail2.png';
import bandoverall from '../assets/images/bandchair/bandoverall.png';

function BandPage(props) {

  return (
    <div>
    <BackButton/>
    <Container>
    <HeroLeftRight imagesource={bandmain} width="100%"
    title="Band Chair"
    text="A pine wood band chair that uses a split-top seat and tapered legs to
    achieve a light and modern look. 14in (L) x 16in (W) x 26in (H)"

    />
    <Spacer/>
    </Container>


    <Container>
    <HeroImage imagesource={banddetail1}/>
    <Spacer />
    <HeroImage imagesource={banddetail2}/>
    <Spacer />
    <HeroImage imagesource={bandoverall}/>
    </Container>
    <ScrollTop />
    </div>
  );
}

export default BandPage
