import React from 'react';
import Psummary from '../components/Psummary';
import HeroLeftRight from '../components/HeroLeftRight';
import CenteredBlurb from '../components/CenteredBlurb';
import Spacer from '../components/Spacer';
import HeroImage from '../components/HeroImage';
import MenuIos from '../components/MenuIos';
import ScrollTop from '../components/ScrollTop';
import CenteredTitle from '../components/CenteredTitle';
import BackButton from '../components/BackButton';
import Container from 'react-bootstrap/Container';

import ioshero from '../assets/images/ios_selection/ios_hero.jpg';
import problem1 from '../assets/images/ios_selection/problem1.jpg';
import problem2 from '../assets/images/ios_selection/problem2.jpg';
import problem3 from '../assets/images/ios_selection/problem3.jpg';
import solution_1 from '../assets/images/ios_selection/solution_1.jpg';
import solution_2 from '../assets/images/ios_selection/solution_2.jpg';
import solution_3 from '../assets/images/ios_selection/solution_3.jpg';

import readingbench from '../assets/images/readingbench/reading_bench.png';
import readingdetail from '../assets/images/readingbench/readingdetail.png';
import readingparts from '../assets/images/readingbench/readingparts.png';
import readingwide from '../assets/images/readingbench/readingwide.png';

function ReadingPage(props) {

  return (
    <div>
    <BackButton/>
    <Container>
    <HeroLeftRight imagesource={readingbench} width="100%"
    title="Pine Reading Bench"
    text="A small reading bench built out of solid pine wood. Its modern and
    rustic design emphasizes the natural grain of the material.
    43in (L) x 15in (W) x 16in (H)"

    />
    <Spacer/>
    </Container>


    <Container>
    <HeroImage imagesource={readingwide}/>
    <Spacer />
    <HeroImage imagesource={readingdetail}/>
    <Spacer />
    <HeroImage imagesource={readingparts}/>
    </Container>
    <ScrollTop />
    </div>
  );
}

export default ReadingPage
