import React from 'react';
import Container from 'react-bootstrap/Container';

import {
  Link
} from "react-router-dom";

import '../index.css';
import '../App.js';



function FolioItem(props) {
  return (
    <div  className ="FolioItem">
      <Link to={props.destination}>
      {props.text && <p className="FolioItemText">{props.text}</p>}
      <img src={props.imagesrc}  alt={props.imagesrc} className="FolioImageClass"/>
      </Link>
    </div>
  );
}

export default FolioItem;
