import React from 'react';
import Psummary from '../components/Psummary';
import CenteredBlurb from '../components/CenteredBlurb';
import HeroImage from '../components/HeroImage';
import ScrollTop from '../components/ScrollTop';
import Container from 'react-bootstrap/Container';
import BackButton from '../components/BackButton';

import permeate from '../assets/images/studies/permeate.png';
import undef from '../assets/images/studies/undefined.png';
import syncopation from '../assets/images/studies/syncopation.png';

function StudiesPage(props) {

  return (
    <div>
    <BackButton/>
      <Container>
      <CenteredBlurb title="Architectural Studies of Space and Movement" smalltitle="A collection
      of imaginary installations based on single-word themes"
      text="
      "/>

      <CenteredBlurb title="Syncopation" smalltitle="Model Study #1"
      text="Syncopation is a musical term defined as a playful deviation from a
      regular rhythmic beat. It is often used to create a mischievous and unpredictable mood.
      This installation uses motion and light to simulate a syncopated spatial
      experience as users wander through the zig-zagging pathway. By interjecting
      regularly sized areas with loud and obnoxious rooms, a playful and unpredictable
      experience can be created.
      "/>
      <HeroImage imagesource={syncopation}/>
      <CenteredBlurb title="Ribbon" smalltitle="Model Study #2"
      text="'Ribbon' explores the distinction between floors, ramps, and stairs.
      This installation questions whether these three sectional components
      can be understood as points on a single spectrum. It is comprised of a single
      ribbon-like pathway that constantly changes in angle and fold density. As users
      walk through the installation, they are repeatedly faced with the task of
      categorizing the surface they are walking on.
      "/>
      <HeroImage imagesource={undef}/>
      <CenteredBlurb title="Permeate" smalltitle="Model Study #3"
      text="This third installation explores the implications of interlocking
      semi-enclosed spaces to create a flowing, interconnected space. By using this
      system of intersecting volumes, users walking through the installation will
      experience a constantly varying sense of enclosure around them.
      "/>
      <HeroImage imagesource={permeate}/>
      </Container>
      <ScrollTop />
    </div>
  );
}

export default StudiesPage;
