import React from 'react';
import Psummary from '../components/Psummary';
import CenteredBlurb from '../components/CenteredBlurb';
import Spacer from '../components/Spacer';
import FullImage from '../components/FullImage';
import ScrollTop from '../components/ScrollTop';
import Menu2 from '../components/Menu2';
import HeroImage from '../components/HeroImage';
import CenteredTitle from '../components/CenteredTitle';
import Container from 'react-bootstrap/Container';
import BackButton2 from '../components/BackButton2';
import Divider from '../components/Divider';
import SizeableImage from '../components/SizeableImage';
import '../index.css';

import functioned from '../assets/images/food/functioned.png';
import defined from '../assets/images/food/defined.jpg';
import scenario2 from '../assets/images/food/scenario2.jpg';
import choose from '../assets/images/food/choose.png';
import page_architecture from '../assets/images/food/page_architecture.jpg';
import wireframes from '../assets/images/food/wireframes.png';
import food_hero2 from '../assets/images/food/food_hero2.jpg';
import food_hero3 from '../assets/images/food/food_hero3.jpg';
import prototyping from '../assets/images/food/prototyping.jpg';
import journey2 from '../assets/images/food/journey2.png';
import journey3 from '../assets/images/food/journey3.png';
import personas from '../assets/images/food/personas.jpg';
import finalscreens1 from '../assets/images/food/finalscreens1.jpg';
import fontcolor from '../assets/images/food/fontcolor.jpg';
import food_hero5 from '../assets/images/food/food_hero5.jpg';
import admockups from '../assets/images/food/admockups.jpg';
import gif1 from '../assets/images/food/food_gif1.gif'

import journeycompiled from '../assets/images/food/journey_compiled.png';
import food_interview from '../assets/images/food/food_interview.png';
import finalscreens2 from '../assets/images/food/finalscreens2.jpg';

import initial_architecture from '../assets/images/food/food_initial_architecture.png';
function FoodPage(props) {

  return (
    <div>
    <div className="divmovedown">
    <BackButton2/>

        <div id="Summary" className="anchor"/>
        <Container>

        <HeroImage imagesource={food_hero5}/>

        <Spacer />

        <Container className="generaltextcontainer">
          <p className="herotext">Foodii Mobile App</p>
          <p className="subtitletext">Designing a solution to help with the age old problem: "where should we eat?"</p>
          <p>Deciding where to go or what to order for a meal is an iterative and often messy process that involves
          balancing multiple variables. Foodii is a mobile app that helps you easily browse restaurants
          while also keeping track of a shortlist.<br/><br/> The scope of this project included ideation,
          user research, wireframing, prototyping, and user testing.</p>
        </Container>
        <Divider />
        <Container className="generaltextcontainer">
          <p className="paragraphtitle">Initial research - what actually makes deciding on a restaurant so difficult?</p>
          <p>This project began with research on existing literature to gain a high level understanding of
          why this problem has become so universally widespread. Listed below are the major factors that were
          discovered to contribute to the problem.
          <br/><br/>
          <b>1. Choice Overload</b>
          <br/>
          Choice overload, or overchoice, is a psychological phenomenon introduced
          by American writer Alvin Toffler in 1970. It states that decision making
          is more difficult when a person is presented with too many options. With the
          enormous amount of different restaurants available to us, particularly
          in dense urban areas, it becomes clear how this phenomenon could contribute to our
          inability to make a decision.
          <br/><br/>
          <b>2. The criteria that we use are constantly changing</b>
          <br/>
          The issue of having too many choices is compounded by the fact that we use a wide
          range of criteria to decide on a restaurant - for instance, its type of cuisine,
          price, and location. These criteria constantly change and are also affected by our
          previous choices (perhaps you do not want to eat steak twice in a row for
            lack of variety).
          </p>
        </Container>
        <Divider />
        <Container className="generaltextcontainer">
        <p className="paragraphtitle">User interviews</p>
        <p>With the high level research completed, user interviews were conducted to
        discover the specific factors that people consider when trying to decide on a
        place to eat, as well as to identify the common pain points and challenges that
        they face in the process.</p>
        <br/>
        <p className="paragraphtitle">Key Findings</p>
        <p>It was discovered that the most commonly recurring factors that users considered when searching for
        a place to eat included the restaurant's:
        <br/><br/>
        1. Location<br/>
        2. Type of cuisine <br/>
        3. Menu pricing<br/>
        4. Reviews <br/> <br/>
        Furthermore, a common frustration of the process was that on some occasions people
        simply don't have any specific criteria in mind. In these instances, respondants reported
        to just browsing around nearby restaurants without any specific goals, or eating at
        the closest or most familiar option.
        </p>
        </Container>
        <SizeableImage imagesource={defined} width="75%"/>
        <Spacer/>
        <Divider />
        <Container className="generaltextcontainer">
        <p className="paragraphtitle">Tackling the problem of choice overload</p>
        <p>In order to begin designing a UX solution to choice overload, a collection of user
        personas were created to fully articulate the criteria that would be used by
        different users when making their restaurant searches. These personas would be
        iteratively sent through drafts of the app in order to ensure a smooth and efficient user journey.
        </p>
        </Container>
        <Spacer/>
        <SizeableImage imagesource={personas} width="85%"/>
        <Spacer/>
        <Container className="generaltextcontainer">
        <p className="paragraphtitle">Diagramming the process of "choosing"</p>
        <p>
        The next step was to diagram out the mental steps that people take when
        choosing a place to eat. This would serve to further guide development of
        the apps page layout.
        <br/><br/>
        From this, it was discovered that the 'choosing process' is often made up of a
        repeating cycle of assessing a restaurant, editing a 'mental shortlist', refining
        search criteria, and making new searches.
        </p>
        </Container>
        <Spacer/>
        <SizeableImage imagesource={choose} width="70%"/>
        <Spacer/><Spacer/>
        <SizeableImage imagesource={functioned} width="85%"/>
        <Spacer/><Spacer/>
        <Container className="generaltextcontainer">
        <p className="paragraphtitle">
        Drafting the app architecture
        </p>
        <p>The initial draft of the apps architecture focused on addressing
        the issues of choice overload and overabundant criteria. The solution that was designed was based around the
        core ideas of providing the user with 2 key tools:<br/><br/>
        1. In order to combat choice overload, the app would provide a <b>shortlist page</b> to help users keep track of different options.<br/>
        2. A <b>search bar with location, cuisine, price, and review filtering </b>to cater to the varied search criteria.
        </p>
        </Container>
        <Spacer/><Spacer/>
        <SizeableImage imagesource={initial_architecture} width="90%"/>
        <Spacer/><Spacer/>
        <Divider/>
        <Container className="generaltextcontainer">
        <p className="paragraphtitle">The issue of choice overload needed to be further addressed</p>
        <p>Upon further analysis of the user journeys through the initial iteration, it was determined
        that the issue of choice overload was not addressed sufficiently. Specifically,
        when users were only presented with a search bar, they would ultimately have to make the same overwhelming
        decision all at once, weighing the variables of location, cuisine, and more when making that single search. <br/><br/>
        Therefore, it was decided that a more curated browsing experience was needed. This ultimately
        resulted in a <b>tabbed navigation system</b> where users could instead begin their searches from an
        explore page, a location-focused page, or a cuisine-focused page. <br/><br/>
        This flattened page heirarchy provided users with a more specific starting point,
        reducing the amount of upfront decisions that needed to be made.
        </p>
        </Container>
        <Spacer/>
        <SizeableImage imagesource={page_architecture} width="90%"/>
        <Spacer/><Spacer/><Spacer/>
        <SizeableImage imagesource={journeycompiled} width="95%"/>
        <Spacer/><Spacer/>
        <Divider/>
        <CenteredBlurb title='Wireframing & Prototyping'
        text=""/>
        <SizeableImage imagesource={wireframes}/>
        <SizeableImage imagesource={prototyping} width="98%"/>
        <Spacer/>
        <Container className="generaltextcontainer">
        <p className="paragraphtitle">User testing</p>
        <p>Digital prototypes were developed in order to conduct usability testing
        of the tabbed navigation system. Two versions were made, where the shortlist was accessed
        either through a dedicated tab at the top of the app, or via an icon on the bottom tab bar.
        These iterations were A/B tested
        to compare their effectiveness for browsing restaurants and modifying the shortlist.
        The qualitative data from the study indicated that the
        version where the shortlist was accessed via the top tab provided a smoother user experience.
        </p>
        </Container>
        <Divider />
        <CenteredBlurb title="Final mockups"/>

        <HeroImage imagesource={food_hero2}/>

        <Container className="generaltextcontainer">
          <p className="captiontitle">Tabbed navigation system</p>
          <p className="captiontext">The tabbed navigation system allows users to cycle between new
          search queries and their shortlist of restaurants
          </p>
        </Container>
        <Divider/>
        </Container>
        <Spacer /><Spacer/>

        <SizeableImage imagesource={food_hero3} width="90%"/>
        <Container className="generaltextcontainer">
          <p className="captiontitle">Order or get directions directly from the restaurant page</p>
          <p className="captiontext">
          </p>
        </Container>
        <Spacer/><Spacer/>
        <SizeableImage imagesource={finalscreens1} />
        <div id="Research" className="anchor"/>


        <div id="Diagramming" className="anchor"/>
        <div id="Mapping" className="anchor"/>


        <div id="Personas" className="anchor"/>
        <div id="Architecture" className="anchor"/>


        <div id="Prototyping" className="anchor"/>



        <div id="Testing" className="anchor"/>





        <ScrollTop />
    </div>
    </div>
  );
}

export default FoodPage
