import React from 'react';
import Psummary from '../components/Psummary';
import LeftRight from '../components/LeftRight';
import CenteredBlurb from '../components/CenteredBlurb';
import Spacer from '../components/Spacer';
import HeroImage from '../components/HeroImage';
import MenuIos from '../components/MenuIos';
import ScrollTop from '../components/ScrollTop';
import CenteredTitle from '../components/CenteredTitle';
import Container from 'react-bootstrap/Container';
import BackButton2 from '../components/BackButton2';
import SizeableImage from '../components/SizeableImage';
import ioshero from '../assets/images/ios_selection/ios_hero.jpg';
import problem1 from '../assets/images/ios_selection/problem1.jpg';
import problem2 from '../assets/images/ios_selection/problem2.jpg';
import problem3 from '../assets/images/ios_selection/problem3.jpg';
import solution_1 from '../assets/images/ios_selection/solution_1.jpg';
import solution_2 from '../assets/images/ios_selection/solution_2.jpg';
import solution_3 from '../assets/images/ios_selection/solution_3.jpg';


function Project2Page(props) {

  return (
    <div>
    <BackButton2 />
      <div className="sidenav">
      <hr class="contentsdivider"/>
        <MenuIos />
      <hr class="contentsdivider"/>
      </div>
    <div className="sidemain">
      <div id="Summary" className="anchor"/>
      <Container>
      <CenteredBlurb title="Improving Text Selection on iOS"
      smalltitle="Using a color-coded control scheme for marking precise text selections"
      text="The current text selection system on iOS 12 faces issues with
      making precise selections. It often feels clunky, and the response
       to changing selections are unpredictable.
      This UI add-on design aims to
      address these issues using a new color-coded attachment to the existing keyboard.
      "/>
      <HeroImage imagesource={ioshero}/>
      </Container>



      <Container>
      <div id="Problems" className="anchorbig"/>


      <CenteredBlurb title="Problem 1"
      smalltitle="Unintentionally covering the text you are selecting"
      text="Precisely selecting small text requires seeing the text that you are trying
      to highlight.
      With the current system, users must put their finger very close
      to the text they are trying to select. For users with larger fingers,
      seeing the text they want to select becomes a problem.
      "/>
      <SizeableImage imagesource={problem1} width="60%"/>

      <CenteredBlurb title="Problem 2"
      smalltitle="Manipulating off-screen selections is not possible"
      text="Consider the scenario below, where the right selection baton is off-screen.
      The correct steps to manipulate the right selection are unclear.
      Holding and dragging the blue selected area to the left does not work, as
      it cancels the current selection.
      Currently, it seems that the only solution is to cancel the current selection
      and redo the selection.
      "/>
      <SizeableImage imagesource={problem2} width="60%"/>

      <CenteredBlurb title="Problem 3"
      smalltitle="The actions overlay blocks content"
      text="The current text actions overlay is located above the text that is
      being selected. While this is helpful to associate the action to the
      current selection, it blocks surrounding text, making reselection
      more difficult.
      "/>
      <SizeableImage imagesource={problem3} width="45%"/>

      <div id="Proposal" className="anchor"/>
      <CenteredBlurb title="Proposed UI Solution"
      text="The proposed solution is an attachment to the iOS keyboard.
      Using two arrow key sets and color coded batons, users can now make more
      precise selections and manipulate the baton when it is off-screen."/>
      <Spacer />
      <HeroImage imagesource={solution_1} />
      <Spacer />
      <HeroImage imagesource={solution_2} />
      <Spacer />

      <CenteredBlurb title="Relocating the
      controls overlay to the bottom of the screen."
      text="Moving the text control overlay to the bottom of the screen
      prevents it from being in the way when you are trying to move
      the baton upwards.
      "/>
      <SizeableImage imagesource={solution_3} width="40%"/>

      </Container>
      <ScrollTop />


    </div>

    </div>
  );
}

export default Project2Page
