import React from 'react';
import Container from 'react-bootstrap/Container';

import '../index.css';

function LeftRight(props) {
  return (
    <div>
      <Container>
      <div className="row ioscontainer">
         <div className="col-sm iosleft">
           {props.number && <h5 className="iosnumber"><strong>{props.number}</strong></h5>}
           {props.title && <h5><strong>{props.title}</strong></h5>}

           {props.text && <p className="lead font-weight-light">{props.text}</p>}
         </div>
         <div className="col-sm sizeimagediv">
         <img src={props.imagesource} width={props.width} alt={props.imagesource} className="sizeableimage"/>
         </div>
      </div>
      </Container>
    </div>
  );
}

export default LeftRight;
