import React from 'react';
import Container from 'react-bootstrap/Container';
import Tab from 'react-bootstrap/Tabs';
import Tabs from 'react-bootstrap/Tabs';

import Spacer from './Spacer';
import '../index.css';

function ImgTab(props) {
  return (
    <Container>
    <Tabs defaultActiveKey="profile" className="imagetab">
      <Tab eventKey="home" title={props.tab1}>
        <Spacer />
        <div>
          <img src={props.imagesource1} className="fillimage" alt={props.imagesource1}/>
        </div>
      </Tab>
      <Tab eventKey="profile" title={props.tab2}>
        <Spacer />
        <div>
          <img src={props.imagesource2} className="fillimage"  alt={props.imagesource2}/>
        </div>
      </Tab>
      <Tab eventKey="contact" title={props.tab3}>
        <Spacer />
        <div>
          <img src={props.imagesource3} className="fillimage"  alt={props.imagesource3}/>
        </div>
      </Tab>
    </Tabs>
    </Container>
  );
}

export default ImgTab;
