import React from 'react';
import Container from 'react-bootstrap/Container';

import '../index.css';

function HeroLeftRight(props) {
  return (
    <div>
      <div class="row">
         <div className="col-sm  heroleftcol">
           {props.title && <h5 class="herolefttitle"><strong>{props.title}</strong></h5>}

           {props.text && <p class="herolefttext">{props.text}</p>}
         </div>
         <div className="col-sm herorightcol">
         <img className="sizeableImage" src={props.imagesource} width={props.width} alt={props.imagesource}/>
         </div>
      </div>

    </div>
  );
}

export default HeroLeftRight;
