import React from 'react';
import Psummary from '../components/Psummary';
import CenteredBlurb from '../components/CenteredBlurb';
import HeroImage from '../components/HeroImage';
import MenuGigeBrand from '../components/MenuGigeBrand';
import ScrollTop from '../components/ScrollTop';
import CenteredTitle from '../components/CenteredTitle';
import Container from 'react-bootstrap/Container';
import BackButton2 from '../components/BackButton2';
import BackButton from '../components/BackButton';
import Divider from '../components/Divider';
import SizeableImage from '../components/SizeableImage';
import Spacer from '../components/Spacer';
import gigelogo_hero from '../assets/images/gige_logo/gigelogo_hero.png';

import gige_cards from '../assets/images/gige_logo/gige_cards.png';
import gige_brochure from '../assets/images/gige_logo/gige_brochure.png';
import gige_handout from '../assets/images/gige_logo/gige_handout.png';
import gige_tradeshow from '../assets/images/gige_logo/tradeshow1.jpg';
import gige_banners from '../assets/images/gige_logo/gige_banners.png';

function GigeLogoPage(props) {

  return (
    <div>
    <BackButton2/>
    <div id="Summary" className="anchor"/>

    <div id="Logo" className="anchor"/>
    <Spacer />
    <CenteredBlurb title="The GIGE Corporation Brand"
    smalltitle=""/>
    <Container>
    <HeroImage imagesource={gigelogo_hero}/>


    </Container>

    <br/><br/><br/>
    <Container>
    <div id="Application" className="anchor"/>

    <br/>



    <HeroImage imagesource={gige_cards}/>
    <HeroImage imagesource={gige_brochure} />
    <HeroImage imagesource={gige_handout} />
    <HeroImage imagesource={gige_banners} />
    <Spacer /><Spacer />
    <SizeableImage imagesource={gige_tradeshow} />

    <div id="Conference" className="anchor"/>
    <div id="Infographic" className="anchor"/>


    <div id="Development" className="anchor"/>
    </Container>
    <ScrollTop />

    </div>
  );
}

export default GigeLogoPage
